/* jQuery.Rating Plugin CSS - http://www.fyneworks.com/jquery/star-rating/ */
.container .popup .edit_rating .radio_div .rating-cancel {display: none !important;}
div.rating-cancel,div.star-rating{float:left;width:18px;height:17px;text-indent:-999em;cursor:pointer;display:block;background:transparent;overflow:hidden;margin-right: 4px;}
div.rating-cancel,div.rating-cancel a{background:url(delete.gif) no-repeat 0 -16px}
div.star-rating,div.star-rating a{background:url(star.png) no-repeat 0 0px}
div.rating-cancel a,div.star-rating a{display:block;width:18px;height:100%;background-position:0 0px;border:0}
div.star-rating-on a{background-position:0 -17px!important}
div.star-rating-hover a{background-position:0 -34px}
/* Read Only CSS */
div.star-rating-readonly a{cursor:default !important}
/* Partial Star CSS */
div.star-rating{background:transparent!important;overflow:hidden!important}
/* END jQuery.Rating Plugin CSS */